import '../../App.css';
import axios from 'axios';
import { useState,useEffect } from 'react';
import { useParams } from 'react-router-dom';



function UpdateNotice(){
    let[title,setTitle] = useState('')
    let[content,setContent] = useState('')
    let[user,setUser] = useState('')
    let [noticeData,setNoticeData] = useState([])
    let {id} = useParams();

 
    useEffect(async() => {
        try{
            const res = await axios.get('http://localhost:3000/notice/detail/data/'+id)
            setNoticeData(res.data)
            console.log('notice update data')
            console.log(res.data)
            setContent(res.data.content)
            setTitle(res.data.title)
        } catch(e){
            console.error(e.message)
        }
        },[])


    useEffect(async()=>{
        try{
            let res = await axios.get('http://localhost:3000/user/who');
            setUser(res.data)
            console.log('notice update user')
            console.log(res.data)
        }catch(err){
            console.log(err)
        }
      },[]);
    
    function titleHandler(e){
        e.preventDefault();
        setTitle(e.target.value)
    }
    function contentHandler(e){
        e.preventDefault();
        setContent(e.target.value)
    }

    function submitHandler(e){
        e.preventDefault();;
        let body = {
            content,
            title,
            user,
            id
        }
        console.log(body)
        axios.post("http://localhost:3000/notice/update",body)
        .then((res)=>{
            window.location='/'
            // window.location='/notice'
        })
        .catch((err)=>{
            console.log(err)
        })
    }

    return(
        <div className='main'>

            <form onSubmit={submitHandler}>
                <div className='notice-upload-section'>
                    <div className='notice-upload-box'>
                        <div className='notice-title-box'>
                            <input input className='notice-upload-title' onChange={titleHandler} value={title}  placeholder='Title'>
                                {/* {noticeData.title} */}
                            </input>
                        </div>
                        <div className='notice-writer-box'>
                            <p className='notice-upload-writer'>writer:  </p>
                        </div>
                        <div className='notice-content-box'>
                            <textarea className='notice-upload-content' value={content} onChange={contentHandler} placeholder='Description'>
                                
                            </textarea>
                        </div>
                        <div className='img-button-wrap'>
                            
                            <button className='upload-btn'  type='submit'></button>
                        </div>
                    </div>
                    
                </div>
            </form>

        </div>
    )
}

export default UpdateNotice;
