/* eslint-disable */
import "../../App.css";
import axios from "axios";
import { Link, useParams } from "react-router-dom";
import { useState, useEffect } from "react";

function NoticeDetail() {
  const [noticeData, setNoticeData] = useState({
    User: { name: "" },
  });
  const [user, setUser] = useState("");
  const [isAuth, setAuth] = useState("");
  const [reply, setReply] = useState([]);

  let { id } = useParams();

  function deleteNotice() {
    // delete 
    console.log(id);
    axios
      .post("http://localhost:3000/notice/delete", { id })
      .then((res) => {
        window.location = "/notice/1";
      })
      .catch((err) => {
        console.log(err);
      });
  }
  useEffect(async () => {
    //            
    try {
      let res = await axios.get("http://localhost:3000/user/who");
      setUser(res.data);
    } catch (err) {
      console.log(err);
    }
  }, []);
  useEffect(async () => {
    try {
      let res = await axios.get("http://localhost:3000/user/authentication");
      setAuth(res.data);
    } catch (err) {
      console.log(err);
    }
  }, []);

  useEffect(async () => {
    try {
      const res = await axios.get(
        "http://localhost:3000/notice/detail/data/" + id
      );
      setNoticeData(res.data);
    } catch (e) {
      console.error(e.message);
    }
  }, []);

  useEffect(async () => {
    //    
    try {
      const res = await axios.get(
        "http://localhost:3000/notice/reply/data/" + id
      );
      setReply(res.data);
    } catch (e) {
      console.error(e.message);
    }
  }, []);

  return (
    <div className="main">
      <div className="section">
        <div className="notice-header">
          <div className="notice-title">{noticeData.title}</div>
          <div className="notice-writer">: {noticeData.User.name}</div>
        </div>
        <hr className="notice-hr"></hr>
        <div className="notice-main">
          <pre>
            <div className="notice-content">{noticeData.content}</div>
          </pre>
        </div>
        {user == noticeData.user_id ? (
          <button onClick={deleteNotice}>delete</button>
        ) : null}
        {user == noticeData.user_id ? (
          <a href={"/notice/update/" + id}>
            <button>update</button>
          </a>
        ) : null}
      </div>

      {isAuth == true ? (
        <div className="reply-input-box">
          {" "}
          <ReplyArea user={user} id={id}></ReplyArea>{" "}
        </div>
      ) : null}

      {reply.map((value, index) => {
        return value.user_id == user ? (
          <div className="reply-box  my-reply">
            <div className="reply-top">
              <div className="reply-writer">: {value.User.name}</div>
              <div className="reply-date">: {value.createdAT}</div>
            </div>
            <hr></hr>

            <div className="reply-content">{value.content}</div>
          </div>
        ) : (
          <div className="reply-box">
            <div className="reply-top">
              <div className="reply-writer">: {value.User.name}</div>
              <div className="reply-date">: {value.createdAT}</div>
            </div>
            <hr></hr>
            <div className="reply-content">{value.content}</div>
          </div>
        );
      })}
    </div>
  );
}
function ReplyArea(props) {
  const [content, setContent] = useState("");

  function contentHandler(e) {
    e.preventDefault();
    setContent(e.target.value);
  }
  function submitHandler(e) {
    // e.preventDefault();;
    let body = {
      content,
      user: props.user,
      id: props.id,
    };
    console.log(body);
    axios
      .post("http://localhost:3000/notice/reply/upload", body)
      .then((res) => {
        // window.location='/'
        // window.location='/notice/1'
      })
      .catch((err) => {
        console.log(err);
      });
  }
  return (
    <>
      <form onSubmit={submitHandler}>
        <textarea onChange={contentHandler} className="reply-input"></textarea>
        <button className="upload-btn" type="submit">
          done
        </button>
      </form>
    </>
  );
}

export default NoticeDetail;
