/* eslint-disable */
import "../../App.css";
import axios from "axios";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";

function App() {
  const [content, setContent] = useState("");
  const [imgTitle, setImgTitle] = useState("");
  const [imgDescription, setImgDescription] = useState("");
  const [user, setUser] = useState("");
  let history = useHistory();
  useEffect(async () => {
    try {
      let res = await axios.get("http://localhost:3000/user/who");
      setUser(res.data);
      console.log("whoami");
      console.log(res.data);
    } catch (err) {
      console.log(err);
    }
  }, []);

  const titleHandler = (e) => {
    e.preventDefault();
    setImgTitle(e.target.value);
    // console.log(imgTitle)
  };
  const desHandler = (e) => {
    e.preventDefault();
    setImgDescription(e.target.value);
    // console.log(imgDescription)
  };
  const onLoadFile = (e) => {
    const file = e.target.files[0];
    setContent(file);
    // console.log(content)
  };

  const handlerClick = (e) => {
    e.preventDefault();
    const formdata = new FormData();
    formdata.append("img", content);
    formdata.append("img_title", imgTitle);
    formdata.append("img_des", imgDescription);
    formdata.append("user_id", user);

    axios
      .post("http://localhost:3000/album/img", formdata)
      .then((res) => {
        console.log("");
        console.log(res);
        window.location = "/album";
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const preview = (fileBlob) => {
    const imgEl = document.querySelector(".upload-img");
    const reader = new FileReader();
    reader.readAsDataURL(fileBlob);
    reader.onload = () => {
      imgEl.style.backgroundImage = `url(${reader.result})`;
    };
  };
  return (
    <div className="main">
      <form enctype="multipart/form-data" onSubmit={handlerClick}>
        <div className="img-upload-section">
          <div className="img-title-input-box">
            <input
              input
              className="img-title-input"
              onChange={titleHandler}
              name="img"
              placeholder="Image Title"
            ></input>
          </div>
          <div className="img-description-input">
            <div className="upload-img">  </div>
            <textarea
              className="img-description"
              onChange={desHandler}
              placeholder="Description"
            ></textarea>
          </div>
          <input
            type="file"
            id="img"
            style={{ display: "none" }}
            onChange={(e) => {
              onLoadFile(e);
              preview(e.target.files[0]);
            }}
            multiple
          ></input>
          <div className="img-button-wrap">
            <label for="img" className="replace-input">
               
            </label>
            <button className="upload-btn" type="submit">
              
            </button>
          </div>
        </div>
      </form>
    </div>
  );
}

export default App;
