import { useState, useEffect } from "react";
import "../../App.css";
import axios from "axios";

function PageCnt(props) {
  let totalPage = props.cnt;
  let currentPage = props.currentPage;
  let cntList = [];
  console.log(props);
  if (totalPage % 10 == 0) {
    totalPage = parseInt(totalPage / 10);
  } else {
    totalPage = parseInt(totalPage / 10) + 1;
  }

  if (totalPage > 5) {
    for (let i = 0; i < 5; i++) {
      cntList.push(currentPage - 2 + i);
    }
  } else {
    for (let i = 1; i <= totalPage; i++) {
      cntList.push(i);
    }
  }
  return (
    <>
      {props.searchCnt}
      <div className="page-bar">
        {props.searchCnt == true
          ? cntList.map((v, i) => {
              return currentPage == v ? (
                <a
                  className="current-page page-btn"
                  href={"/notice/search/" + props.search + "/" + v}
                >
                  {" "}
                  {v}{" "}
                </a>
              ) : (
                <a
                  className="page-btn"
                  href={"/notice/search/" + props.search + "/" + v}
                >
                  {" "}
                  {v}{" "}
                </a>
              );
            })
          : cntList.map((v, i) => {
              return currentPage == v ? (
                <a className="current-page page-btn" href={"/notice/" + v}>
                  {" "}
                  {v}{" "}
                </a>
              ) : (
                <a className="page-btn" href={"/notice/" + v}>
                  {" "}
                  {v}{" "}
                </a>
              );
            })}
      </div>
    </>
  );
}
export default PageCnt;
