/* eslint-disable */
import "./App.css";
import { Route, Switch } from "react-router-dom";
import Profile from "./routes/profile/Profile";
import Album from "./routes/album/Album";
import PostImg from "./routes/album/PostImg";
import LoginPage from "./routes/login/LoginPage";
import Nav from "./routes/Nav";
import Signup from "./routes/login/Signup";
import StartPage from "./routes/StartPage";
import NotFound from "./routes/NotFound";
import NoticePost from "./routes/notice/NoticePost";
import Notice from "./routes/notice/Notice";
import Cal from "./routes/cal/Cal";
import CalInput from "./routes/cal/CalInput";
import ChatBotCoponent from "./routes/profile/ChatBotComponent";
import NoticeDetail from "./routes/notice/NoticeDetail";
import NoticeSearch from "./routes/notice/NoticeSearch";

function App() {
  return (
    <div className="container">
      <Nav></Nav>

      <Switch>
        <Route exact path="/profile">
          <Profile></Profile>
        </Route>
        <Route exact path="/album/upload/img">
          <PostImg></PostImg>
        </Route>
        <Route path="/album">
          <Album></Album>
        </Route>
        <Route exact path="/auth/login">
          <LoginPage></LoginPage>
        </Route>
        <Route exact path="/auth/signup">
          <Signup></Signup>
        </Route>
        <Route exact path="/notice/upload">
          <NoticePost></NoticePost>
        </Route>
        <Route exact path="/notice/search/:search/:page">
          <NoticeSearch></NoticeSearch>
        </Route>
        <Route exact path="/notice/detail/:id">
          <NoticeDetail></NoticeDetail>
        </Route>
        <Route path="/notice/:page">
          <Notice></Notice>
        </Route>
        <Route exact path="/cal/input">
          <CalInput></CalInput>
        </Route>
        <Route exact path="/cal">
          <Cal></Cal>
        </Route>

        <Route exact path="/">
          <StartPage></StartPage>
        </Route>
        <Route path="*">
          <NotFound></NotFound>
        </Route>
      </Switch>
      <ChatBotCoponent></ChatBotCoponent>
    </div>
  );
}

export default App;
