/* eslint-disable */

import '../App.css';
import logo2 from '../logo192.png';
import {useState,useEffect} from 'react';
import axios from 'axios';
import {Link,Route,Switch} from 'react-router-dom';




function Nav(){
    const [isAuth,setAuth] = useState('')

    useEffect(async()=>{
        try{
            let res = await axios.get('http://localhost:3000/user/authentication');
            setAuth(res.data)       
        }catch(err){
            console.log(err)
        }
    },[]);

    return(
        <nav className='navbar'>
            <div className='home-tab'>
                <a href='/'><img className='home-logo' src={logo2}></img></a>
            </div>
            <div className='menu-bar'>
                <a href='/profile' className='nav-menu'>Profiles</a>
                <a href='/cal' className='nav-menu'>Calendar</a>
                <a href='/notice/1' className='nav-menu'>Notice</a>
                <a href='/album' className='nav-menu'>Album</a>
                {
                    isAuth==true?<a href='/auth/user/logout'  className='nav-menu'>Logout</a>:<a href='/auth/login' className='nav-menu'>Login</a>
                }
            </div>
        </nav>
    )

}

export default Nav;