/* eslint-disable */
import '../../App.css';
import {useState,useEffect} from 'react';
import axios from 'axios';



function CalInput(){ 
    const [title,setTitle] = useState('')
    const [start , setStart]= useState('')
    const [end , setEnd]= useState('')
    const [user,setUser] = useState('')



    useEffect(async()=>{
        try{
            let res = await axios.get('http://localhost:3000/user/who');
            setUser(res.data)
            // console.log('whoami')
            // console.log(res.data)
        }catch(err){
            console.log(err)
        }
      },[]);
    
    function titleHandler(e){
        e.preventDefault();
        setTitle(e.target.value)
    }

    function startHandler(e){
        e.preventDefault();
        setStart(e.target.value);
    }

    function endHandler(e){
        e.preventDefault();
        setEnd(e.target.value);
    }   

    const submitHandler = (e)=>{
        e.preventDefault();
        let body = {
            title,
            start,
            end,
            id:user,
        }
        axios
            .post("http://localhost:3000/cal/data", body)
            .then((res) =>{
                console.log('res')
                console.log(res)
                window.location = '/cal'
            })
            .catch((err)=>(console.log(err)));
    }


    return(
        <div className='section'>
            <form onSubmit={submitHandler}>
                <table className='cal-input-box'>
                    <tr>
                        <td className='cal-td'>: </td>
                        <td className='cal-td'><input type={'text'} name='title' onChange={titleHandler}></input></td>
                    </tr>   

                    <tr>
                        <td className='cal-td'> </td>
                        <td className='cal-td'><input type={'date'} name='start'onChange={startHandler}></input></td>
                    </tr>
                    
                    <tr>
                        <td className='cal-td'> </td>
                        <td className='cal-td'><input type={'date'} name='end' onChange={endHandler}></input></td>
                     </tr>
                </table>
                <button>done</button>
            </form>

        </div>
    )
}

export default CalInput;