/* eslint-disable */
import "../../App.css";
import { useState } from "react";
import axios from "axios";

function Signup() {
  const [id, setId] = useState("");
  const [pwd, setPwd] = useState("");
  const [name, setName] = useState("");
  function idHandler(e) {
    e.preventDefault();
    setId(e.target.value);
  }
  function pwdHandler(e) {
    e.preventDefault();
    setPwd(e.target.value);
  }
  function nameHandler(e) {
    e.preventDefault();
    setName(e.target.value);
  }
  const submitHandler = (e) => {
    e.preventDefault();
    // state   .
    console.log(pwd);
    let body = {
      id: id,
      pwd: pwd,
      name: name,
    };
    console.log(body);
    axios
      .post("http://localhost:3000/auth/signup", body)
      .then((res) => {
        console.log("res");
        console.log(res);
        if (res.data.id == 1) {
          window.location = "/auth/login";
          alert(" ");
        } else {
          window.location = "/auth/signup";
          alert(res.data.message);
        }
      })
      .catch((err) => {
        console.log(err);
      });
    console.log("fds");
  };
  return (
    <div className="main">
      <div className="login-box">
        <h1>Sign UP</h1>
        <form onSubmit={submitHandler}>
          <div className="txt-field">
            <input
              type="text"
              required
              name="id"
              onChange={idHandler}
              placeholder=""
            ></input>
            <span></span>
          </div>
          <div className="txt-field">
            <input
              type="password"
              name="pwd"
              onChange={pwdHandler}
              placeholder=""
              required
            ></input>
            <span></span>
          </div>

          <div className="txt-field">
            <input
              type="text"
              name="name"
              onChange={nameHandler}
              placeholder=""
            ></input>
            <span></span>
          </div>

          <input type="submit" value="Sign UP"></input>
          <div className="signup-link">
            <a href="/auth/login">Go to Login</a>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Signup;
