import { useState,useEffect } from 'react';
import '../../App.css';
import axios from 'axios';

function NoticePost(){
    const [content, setContent] = useState('');
    const [title,setTitle] = useState('');
    const [user,setUser] = useState('')
    
    useEffect(async()=>{
      try{
          let res = await axios.get('http://localhost:3000/user/who');
          setUser(res.data)
          console.log('whoami')
          console.log(res.data)
      }catch(err){
          console.log(err)
      }
    },[]);

    function titleHandler(e){
        e.preventDefault();
        setTitle(e.target.value)
    }
    function contentHandler(e){
        e.preventDefault();
        setContent(e.target.value)
    }
    function submitHandler(e){
        e.preventDefault();;
        let body = {
            content,
            title,
            user,
        }
        console.log(body)
        axios.post("http://localhost:3000/notice/upload",body)
        .then((res)=>{
            // window.location='/'
            window.location='/notice/1'
        })
        .catch((err)=>{
            console.log(err)
        })
    }
    return(
        <div>
            <div className='main'>
                <form onSubmit={submitHandler}>
                    <div className='notice-upload-section'>
                        <div className='notice-upload-box'>
                            <div className='notice-title-box'>
                                <input input className='notice-upload-title' onChange={titleHandler}  placeholder='Title'></input>
                            </div>
                            <div className='notice-writer-box'>
                                <p className='notice-upload-writer'>writer: {user} </p>
                            </div>
                            <div className='notice-content-box'>
                                <textarea className='notice-upload-content' onChange={contentHandler} placeholder='Description'></textarea>
                            </div>
                            <div className='img-button-wrap'>
                             
                                <button className='upload-btn'  type='submit'></button>
                            </div>
                        </div>
                        
                    </div>
                </form>
            </div>
        </div>
    )
}

export default NoticePost;