import "../../App.css";
import { useState, useEffect } from "react";
import axios from "axios";
import writeImg from "../../camera.svg";
import Image from "./Image.js";
import { Link, Route, Switch } from "react-router-dom";
import NotFound from "../NotFound";

function Album() {
  const [img, imgChange] = useState({ name: "", etc: "" });
  const [imgUrl, setImgUrl] = useState([]);
  const [isAuth, setAuth] = useState("");
  const [user, setUser] = useState("");

  useEffect(async () => {
    try {
      let res = await axios.get("http://localhost:3000/user/who");
      setUser(res.data);
    } catch (err) {
      console.log(err);
    }
  }, []);

  useEffect(async () => {
    try {
      let res = await axios.get("http://localhost:3000/album/data/photo");
      imgChange(res.data);
      for (let i = 0; i < res.data.length; i++) {
        imgUrl.push(
          "http://localhost:3000/img/" +
            res.data[i].imgLocate +
            "/" +
            res.data[i].imgName
        ); // 
      }
    } catch (err) {
      console.log(err);
    }
  }, []);

  useEffect(async () => {
    try {
      let res = await axios.get("http://localhost:3000/user/authentication");
      setAuth(res.data);
    } catch (err) {
      console.log(err);
    }
  }, []);
  console.log(img);

  return (
    <div className="main">
      <Switch>
        <Route exact path="/album">
          <div id="img-section">
            <div className="img-page-header">
              <h1 className="page-name">photo</h1>
              {isAuth == true ? (
                <a className="camera-btn-a" href="/album/upload/img">
                  <img className="camera-icon" src={writeImg}></img>
                </a>
              ) : null}
            </div>
            <div className="imgSection">
              {imgUrl.map((value, index) => {
                return (
                  <Link to={"/album/image/" + index} className="img-link">
                    <img className="photo" src={value}></img>
                  </Link>
                );
              })}
            </div>
          </div>
        </Route>

        <Route exact path="/album/image/:id">
          <Image img={img} url={imgUrl} user={user} />
        </Route>
        <Route path="*">
          <NotFound></NotFound>
        </Route>
      </Switch>
    </div>
  );
}

export default Album;
