import { useState, useEffect } from "react";
import "../../App.css";
import axios from "axios";
import { Link, Route, Switch, useParams } from "react-router-dom";
import NotFound from "../NotFound";
import NoticeDetail from "./NoticeDetail";
import UpdateNotice from "./UpdateNotice";
import PageCnt from "./PageCnt";

let qna = [];

function Notice() {
  let [noticeData, setNoticeData] = useState([]);
  const [isAuth, setAuth] = useState("");
  const [searchWord, setSearch] = useState("");
  const [searchCnt, setSearchCnt] = useState(false);

  let [cnt, setCnt] = useState();
  let { page } = useParams();

  function searchHandler(e) {
    e.preventDefault();
    console.log(e.target.value);
    setSearch(e.target.value);
  }
  useEffect(async () => {
    try {
      //      await  
      const res = await axios.get("http://localhost:3000/notice/data/" + page);
      setNoticeData(res.data);
    } catch (e) {
      console.error(e.message);
    }
  }, []);
  useEffect(async () => {
    try {
      let res = await axios.get("http://localhost:3000/user/authentication");
      setAuth(res.data);
    } catch (err) {
      console.log(err);
    }
  }, []);

  useEffect(async () => {
    try {
      const res = await axios.get("http://localhost:3000/notice/page/cnt");
      setCnt(res.data);
    } catch (err) {
      console.log(err);
    }
  }, []);

  return (
    <>
      <Switch>
        <Route exact path="/notice/:page">
          <div className="section">
            <h1 className="notice-community">community</h1>
            <div className="notice-search-box">
              <input
                type="text"
                className="search-ipnut"
                onChange={searchHandler}
              ></input>
              <a href={`/notice/search/` + searchWord + `/1`}>
                <button className="search-btn"></button>
              </a>
            </div>

            <div className="table">
              <table id="qnaTable">
                <tr>
                  <td>no</td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                {noticeData.map((a, i) => {
                  return (
                    <tr>
                      <td>
                        <a
                          className="notice-list"
                          href={"/notice/detail/" + noticeData[i].id}
                        >
                          {noticeData[i].id}
                        </a>{" "}
                      </td>
                      <td>
                        <a
                          className="notice-list"
                          href={"/notice/detail/" + noticeData[i].id}
                        >
                          {noticeData[i].User.name}
                        </a>{" "}
                      </td>
                      <td>
                        <a
                          className="notice-list"
                          href={"/notice/detail/" + noticeData[i].id}
                        >
                          {noticeData[i].title}
                        </a>{" "}
                      </td>
                      <td>
                        <a
                          className="notice-list"
                          href={"/notice/detail/" + noticeData[i].id}
                        >
                          {noticeData[i].createdAt}
                        </a>{" "}
                      </td>
                    </tr>
                  );
                })}
              </table>
            </div>
            {isAuth == true ? (
              <div className="notice-upload-btn">
                <a href={"/notice/upload"}>
                  <button></button>
                </a>{" "}
              </div>
            ) : null}
            <PageCnt
              currentPage={page}
              cnt={cnt}
              searchCnt={searchCnt}
            ></PageCnt>
          </div>
        </Route>
        <Route exact path="/notice/update/:id">
          <UpdateNotice></UpdateNotice>
        </Route>

        <Route path="*">
          <NotFound></NotFound>
        </Route>
      </Switch>
    </>
  );
}

export default Notice;
