import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import { useState,useEffect } from "react";
import axios from "axios";

import 'react-big-calendar/lib/css/react-big-calendar.css';

function Cal() {
  const [calData, setCalData] = useState('');
  const [isAuth,setAuth] = useState('')
  const [user,setUser] = useState('');
  
  const onLoad = async ()=>{
    let res = await axios.get('http://localhost:3000/cal/data');
    setCalData(res.data.map((el)=>{return {end :new Date(Date.parse(el.end)),start : new Date(Date.parse(el.start)),id : el.id, title :el.title}}))
  }

  useEffect(async()=>{  
    try{
       onLoad();
        let user = await axios.get('http://localhost:3000/user/who');
        setUser(user.data)
    }catch(err){
        console.log(err)
    }
  },[]);

  useEffect(async()=>{  //
    try{
        // console.log('aa')
        let res = await axios.get('http://localhost:3000/user/authentication');
        setAuth(res.data)
        // console.log('set auth')
        // console.log(res.data)
    }catch(err){
        console.log(err)
    }
  },[]);


    moment.locale('ko-KR');
    const localizer = momentLocalizer(moment);

    return (
        <div className='section' >
            <Calendar
            localizer={localizer}
            style={{ height: '500px',width:'90%'  }}
            events={calData}
            onSelectEvent={(event)=>{axios.delete('http://localhost:3000/cal/delete',{data:{id:event.id,user:user}}).then(res=>{console.log(res);onLoad();}).catch(err=>alert(err.message))}}
            components={{
                toolbar: Toolbar,
            }}
            />
            {
              isAuth == true? <a href='/cal/input'><button></button></a>:null
            }
            

        </div>


    )
}

function Toolbar(props) {
    const {
      date,
    } = props;
  
    const navigate = (action) => {
      props.onNavigate(action);
    };
  
    return (
      <div className="rbc-toolbar">
        <span className="rbc-btn-group">
          <button type="button" onClick={navigate.bind(null, 'TODAY')}>
            
          </button>
          <button
            type="button"
            onClick={navigate.bind(null, 'PREV')}
          >
            
          </button>
          <span className="rbc-toolbar-label">{`${date.getFullYear()} ${date.getMonth() + 1}`}</span>
          <button
            type="button"
            onClick={navigate.bind(null, 'NEXT')}
          >
            
          </button>
        </span>
      </div>
    );
  }



export default Cal;