/* eslint-disable */
import "../../App.css";
import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import axios from "axios";

function Image(props) {
  let { id } = useParams();
  console.log("");

  function deleteImg() {
    let body = {
      imgName: props.img[id].imgName,
      imgNum: props.img[id].id,
    };
    axios
      .post("http://localhost:3000/album/img/delete", body)
      .then((res) => {
        console.log(res);
        window.location = "/album";
      })
      .catch((err) => {
        err;
      });
  }

  return (
    <div className="main">
      <div className="detail-img-wrap">
        <div className="detail-img-left">
          <img className="detail-img" src={props.url[id]}></img>
        </div>
        <div className="detail-img-right">
          <div className="detail-img-title">
            <h1>: {props.img[id].imgTitle}</h1>
          </div>
          <div className="detail-img-writer">
            <h2>: {props.img[id].User.name}</h2>
          </div>
          <div className="detail-img-desc">
            <pre>{props.img[id].imgDescription}</pre>
          </div>
          {props.user == props.img[id].user_id ? (
            <button onClick={deleteImg}></button>
          ) : null}
        </div>
      </div>
      
    </div>
  );
}

export default Image;
