import '../../App.css';
import { useEffect, useState } from 'react';
import axios from 'axios';


function LoginPage(){
  const [id,setId]= useState('');
  const [pwd, setPwd] = useState('');


  function idHandler(e){
      e.preventDefault();
      setId(e.target.value);
  }
  function pwdHandler(e){
      e.preventDefault();
      setPwd(e.target.value);
  }
  const submitHandler = (e) => {
    e.preventDefault();
    // state   .
    console.log(pwd)
    let body = {
        id : id,
        pwd : pwd
    }
    console.log(body)
    axios
      .post("http://localhost:3000/auth/user/login", body)
      .then((res) =>{
          window.location='/'
      })
      .catch((err)=>{
        window.location='/auth/login'
        alert('   ')
      });
  };
    return(
      <div className='main'>
        <div className="login-box">
        <h1>Login</h1>
        <form onSubmit={submitHandler}>
          <div className="txt-field">
            <input type="text" required name="id"  onChange={idHandler} placeholder=""></input>
            <span></span>

          </div>
          <div className="txt-field">
            <input type="password" name="pwd" onChange={pwdHandler} placeholder="" required></input>
            <span></span>
          </div>

          <input type="submit" value="Login"></input>
          <div className="signup-link">
            <a href="/auth/signup">Signup</a>
          </div>
        </form>
      </div>
      </div>
    )
}

export default LoginPage;
